  margin: 0 9px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #3b434c;
  opacity: 0.35;
  transform: scale(1);
  transition: opacity 0.3s, color 0.1s, transform 0.15s; }
  .team__dot:hover {
    opacity: 0.6; }

.team__pagination {
  display: flex;
  justify-content: center; }

@media screen and (min-width: 310px) {
  .team__wrapper {
    height: 1500px; } }

@media screen and (min-width: 380px) {
  .team__wrapper {
    height: 1350px; } }

@media screen and (min-width: 640px) {
  .team__wrapper {
    height: 900px; }
  .team__group {
    margin: 0 20px; }
  .team__person {
    width: calc(50% - 20px); }
  .team__photo {
    width: 100%;
    height: 220px; }
  .team__photo-line {
    display: block;
    top: 215px; }
  .team__header {
    margin: 29px 0;
    font-size: 1.4em; }
  .team__social {
    margin-bottom: 24px; } }

@media screen and (min-width: 940px) {
  .team__wrapper {
    height: 550px;
    overflow: hidden; }
  .team__group {
    margin: 0; }
  .team__person {
    width: calc(25% - 30px); } }

@media screen and (min-width: 1200px) {
  .team__photo {
    height: 280px; }
  .team__photo-line {
    top: 275px; } }

.page__contact {
  position: relative;
  padding: 70px 0 70px 0;
  overflow: hidden;
  background-image: linear-gradient(rgba(59, 67, 76, 0.9) 0%, rgba(35, 40, 46, 0.9) 100%), url("img/contact_bg_768.jpg");
  background-size: cover;
  background-position: center center;
  color: #ffffff; }

.contact__content {
  padding: 0; }

.contact__header {
  color: #fff; }

.contact__subheader {
  text-align: center;
  line-height: 1.875em;
  width: 60%;
  margin: -12px auto 90px auto; }

.contact__form {
  display: flex;
  flex-direction: column;
  padding: 0 10px; }

.contact__row {
  margin-bottom: 30px; }
  .contact__row.contact__row--top {
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
  .contact__row.contact__row--bottom {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center; }

.contact__item {
  position: relative;
  width: 100%; }
  .contact__item.contact__item--message {
    width: 100%; }

.contact__label {
  font-size: 0.8em;
  text-transform: uppercase;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  transition: transform 0.3s, opacity 0.3s; }

.contact__input {
  width: 100%; }
  .contact__input:focus + label {
    transform: translateY(-120%);
    opacity: 1; }
  .contact__input.contact__input--email {
    margin-bottom: 30px; }
  .contact__input.contact__input--message {
    resize: vertical; }

.contact__social {
  display: flex;
  margin: 0; }

.contact__social-item {
  width: 43px;
  height: 43px;
  line-height: 43px;
  text-align: center;
  color: #fff;
  transition: transform 0.2s;
  transform: scale(1); }
  .contact__social-item:not(:first-of-type) {
    margin: 0 0 0 20px; }
  .contact__social-item a {
    display: block;
    width: 100%;
    height: 100%; }
  .contact__social-item:hover {
    transform: scale(0.9); }

.contact__social-icon {
  width: 100%;
  height: 100%; }

.contact__submit {
  margin-bottom: 40px; }

@media screen and (min-width: 768px) {
  .page__contact {
    padding: 70px 0 100px 0;
    background-image: linear-gradient(rgba(59, 67, 76, 0.9) 0%, rgba(35, 40, 46, 0.9) 100%), url("img/contact_bg_960.jpg"); }
  .contact__form {
    padding: 0px; }
  .contact__row.contact__row--top {
    flex-direction: row; }
  .contact__row.contact__row--bottom {
    flex-direction: row; }
  .contact__item {
    width: calc(50% - 15px); }
  .contact__input.contact__input--email {
    margin-bottom: 0; }
  .contact__submit {
    margin-bottom: 0px; } }

@media screen and (min-width: 960px) {
  .page__contact {
    background-image: linear-gradient(rgba(59, 67, 76, 0.9) 0%, rgba(35, 40, 46, 0.9) 100%), url("img/contact_bg_1200.jpg"); } }

@media screen and (min-width: 1200px) {
  .page__contact {
    background-image: linear-gradient(rgba(59, 67, 76, 0.9) 0%, rgba(35, 40, 46, 0.9) 100%), url("img/contact_bg_1920.jpg"); }
  .contact__content {
    padding: 0 155px; } }

.page__footer {
  height: 120px;
  line-height: 40px;
  font-size: 80%;
  padding: 20px 0;
  background-color: #3b434c;
  color: #ffffff;
  overflow: hidden; }

.footer__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase; }

.footer__logo {
  height: 1em;
  vertical-align: middle;
  transition: opacity 0.3s; }
  .footer__logo:hover {
    opacity: 0.5; }

@media screen and (min-width: 360px) {
  .page__footer {
    height: 105px;
    line-height: 105px;
    padding: 0; }
  .footer__wrapper {
    flex-direction: row;
    padding: 0 10px; } }

@media screen and (min-width: 480px) {
  .page__footer {
    font-size: 100%; } }

@media screen and (min-width: 940px) {
  .footer__wrapper {
    padding: 0px; } }
